<script>
	const App = getApp();
	export default {
		data() {
			return {
				// baseUrl: 'https://weixin.uesg.org.cn',
				baseUrl: 'https://www.scdpf.xyz',
				// baseUrl: 'http://192.168.1.6:39001',
				imageHeight: 0,
				marginTop: 200,
				marginBottom: 82,
				bannerTop: 0,
				type: '',
				show: false,
				loading: true,
				cardInfor: {
					pinyin: "Rong Yu Si",
					name: "司容宇",
					job: "董 事",
					tel: "133 3199 8888",
					email: "seven@uesg.cn"
				},
				clamShow: false
			};
		},
		methods: {
			showClamText() {
				this.clamShow = true
				setTimeout(() => {
					// 关闭 loading，展示页面内容
					this.clamShow = false
				}, 1500); 
			},
			copyCardInfo() {
				const that = this;
				// 复制内容
				uni.setClipboardData({
					data: that.cardInfor.name + '·' + that.cardInfor.job + '·UESG优世界（中国）',
					success: function() {
						//调用方法成功
						console.log('success');
						uni.showToast({
							title: '复制成功',
							duration: 1000 //单位毫秒
						});
					}
				})
			},
			saveCardInfo() {
				const that = this;
				uni.addPhoneContact({
					// nickName: that.cardInfor.name,
					lastName: that.cardInfor.name.charAt(0),
					firstName: that.cardInfor.name.substring(1),
					mobilePhoneNumber: that.cardInfor.tel,
					email: that.cardInfor.email,
					organization: "UESG优世界（中国）",
					title: that.cardInfor.job,
					success: function() {
						console.log('success');
						uni.showToast({
							title: '添加成功',
							duration: 1000 //单位毫秒
						});
					},
					fail: function() {
						console.log('fail');
					}
				});
			},

			init() {
				const that = this;
				uni.request({
					url: that.baseUrl + "/uesg/getInfo",
					method: "POST",
					data: {
						unionID: App.globalData.unionID
					},
					success(res) {
						console.log(res.data);
						that.cardInfor = {
							pinyin: res.data.pinyin,
							name: res.data.realname,
							job: res.data.job,
							tel: res.data.tel_num,
							email: res.data.email
						}
						console.log("名片信息", that.cardInfor);
					}
				})
				// uni.request({
				// 	url: that.baseUrl + "/card/info?unionID=" + App.globalData.unionID,
				// 	method: "GET",
				// 	success(res) {
				// 		console.log(res.data);
				// 		that.cardInfor = {
				// 			pinyin: res.data.data.pinyin,
				// 			name: res.data.data.realname,
				// 			job: res.data.data.job,
				// 			tel: res.data.data.tel_num,
				// 			email: res.data.data.email
				// 		}
				// 		console.log("名片信息", that.cardInfor);
				// 	}
				// })
			},
			change(e) {
				this.show = e.show
			},
			toggle(type) {
				this.type = type
				// open 方法传入参数 等同在 uni-popup 组件上绑定 type属性
				this.$refs.popup.open(type)
			},
			closePopup() {
				this.$refs.popup.close()
			},
			getImageHeight() {
				//.box获取class为box的元素，如果使用的id= 'box' 则使用'#box'
				uni.createSelectorQuery().select('.banner').boundingClientRect(data => {
					console.log(data)
					this.imageHeight = data.height;
					this.marginTop = data.height - 30;
				}).exec()
			},
			gotoGzh() {
				uni.reLaunch({
					url: '/pages/subpkg/pay/pay'
				})
			},
			call(num) {
				uni.makePhoneCall({
					phoneNumber: num //仅为示例
				});
			},

			copy(value) {
				// 复制内容
				uni.setClipboardData({
					data: value + '', // 这里是个坑接受字符串类型 value转化为字符串
					success: function() {
						//调用方法成功
						console.log('success');
						uni.showToast({
							title: '复制成功',
							duration: 1000 //单位毫秒
						});
					}
				})
			},

		},
		onLoad() {
			this.init();
			setTimeout(() => {
				// 关闭 loading，展示页面内容
				this.loading = false;
			}, 1200); 
		},
		onReady() {
			//状态栏高度
			this.iStatusBarHeight = uni.getSystemInfoSync().statusBarHeight;
			//胶囊高度
			// 获取微信胶囊的位置信息 width,height,top,right,left,bottom
			this.customHeight = wx.getMenuButtonBoundingClientRect()
			// 导航栏高度(标题栏高度) = 胶囊高度 + (顶部距离 - 状态栏高度) * 2
			this.navigationBarHeight = this.customHeight.height + (this.customHeight.top - this.iStatusBarHeight) * 2
			// 总体高度 = 状态栏高度 + 导航栏高度
			this.navHeight = this.navigationBarHeight + this.iStatusBarHeight

			this.bannerTop = this.customHeight.top;
			// this.getImageHeight();
		},
		onShow() {
			this.init();
			this.setTabBarIndex(1);
			// this.setTabBarRed();


		},
		onShareAppMessage: function() {
			return {
				title: this.cardInfor.name + " 分享 UESG优世界（中国）电子名片给您。",
				imageUrl: "https://www.uesg.cn/static/39711709043710_.pic.jpg",
				path: "/pages/index/index?unionID=" + App.globalData.unionID
			};
		},
		onShareTimeline: function() {
			return {
				title: this.cardInfor.name + " 分享 UESG优世界（中国）电子名片给您。",
				imageUrl: "https://www.uesg.cn/static/39711709043710_.pic.jpg",
				path: "/pages/index/index?unionID=" + App.globalData.unionID
			}
		},

	};
</script>

